.list {
  color: #fff;
  width: 300px;
  background-color: #bf07ac;
  height: 50px;
  margin: 20px auto;
  border-radius: 5px;
  text-align: center;
}

.checkmark {
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  margin-right: 7.5px;
  background-color: #eee;
  display: inline-block;
}

input[type="checkbox"]:checked + input.text {
  text-decoration: line-through;
  color: black;
}

.list p {
  margin: auto;
  padding: 10px;
  text-align: center;
}

.list p input {
  background-color: transparent;
  border: #fff;
  font-size: 18px;
  color: white;
}

#trash {
  background-color: transparent;
  border: 0;
  color: white;
  font-size: 20px;
  display: inline-block;
  right: auto;
}
